/* Polygonal blue background for glassmorphic login */
.glass-bg {
  min-height: 100vh;
  width: 100vw;
  /* Using process.env.PUBLIC_URL + '/login_background.jpg' in App.js */
  background: linear-gradient(120deg, rgba(21,101,192,0.35) 0%, rgba(33,150,243,0.2) 60%, rgba(144,202,249,0.08) 100%);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  align-items: center;
  justify-content: center;
}

.glass-split-container {
  display: flex;
  width: 1100px;
  max-width: 98vw;
  min-height: 600px;
  background: none;
  border-radius: 24px;
  box-shadow: 0 8px 36px rgba(25, 118, 210, 0.12);
  position: relative;
  align-items: center;
  justify-content: center;
}

.glass-card-panel {
  flex: 1.05;
  display: flex;
  background: none;
  padding: 0 2.5rem;
  min-width: 340px;
  height: 100%;
}
.center-panel {
  position: relative; /* for top-right controls */
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translateX(40px); /* nudge right for visual centering */
}

.top-right-controls {
  position: fixed;
  top: 24px;
  right: 32px;
  z-index: 100;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.switch-toggle-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4px;
}

.switch {
  position: relative;
  display: inline-block;
  width: 48px;
  height: 24px;
}
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #e3f2fd;
  border-radius: 24px;
  transition: background 0.3s;
}
.switch input:checked + .slider {
  background: #1976d2;
}
.slider:before {
  position: absolute;
  content: "";
  height: 18px;
  width: 18px;
  left: 3px;
  bottom: 3px;
  background-color: #fff;
  border-radius: 50%;
  transition: transform 0.3s;
  box-shadow: 0 2px 6px rgba(25,118,210,0.18);
}
.switch input:checked + .slider:before {
  transform: translateX(24px);
}
.toggle-label-below {
  font-size: 0.98rem;
  color: #e3f2fd;
  margin-top: 2px;
  font-weight: 500;
  letter-spacing: 0.5px;
}

.mode-toggle {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #fff;
  font-weight: 600;
}
.mode-toggle input[type="checkbox"] {
  width: 40px;
  height: 20px;
}
.mode-toggle .toggle-label {
  margin-left: 6px;
  color: #e3f2fd;
}

.signup-prompt {
  width: 100%;
  text-align: center;
  color: #e3f2fd;
  margin-bottom: 0.6rem;
}
.signup-prompt a {
  color: #fff;
  text-decoration: underline;
  margin-left: 6px;
  cursor: pointer;
}
.glass-card {
  background: rgba(255,255,255,0.18);
  border-radius: 18px;
  box-shadow: 0 0 24px 2px #90caf9, 0 4px 32px rgba(25, 118, 210, 0.18);
  padding: 2.2rem 2.2rem 2rem 2.2rem;
  width: 100%;
  max-width: 340px;
  display: flex;
  flex-direction: column;
  align-items: center;
  backdrop-filter: blur(16px);
  border: 1.5px solid rgba(144,202,249,0.45);
  position: relative;
  z-index: 2;
}
.glass-card-title {
  font-size: 30px;
  font-family: 'Times New Roman', Times, serif;
  font-weight: 600;
  color: white;
  margin-bottom: 1.2rem;
  text-align: center;
  letter-spacing: 0.5px;
}
.glass-card-group {
  width: 100%;
  margin-bottom: 1.1rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  background: rgba(255,255,255,0.32);
  border-radius: 8px;
  box-shadow: 0 1px 4px rgba(25,118,210,0.07);
  padding: 0.1rem 0.7rem 0.1rem 0.7rem;
}
.glass-icon {
  color: #1976d2;
  font-size: 1.1rem;
  margin-right: 0.7em;
  opacity: 0.85;
}
.glass-card-group input {
  flex: 1;
  border: none;
  background: transparent;
  outline: none;
  font-size: 1rem;
  color: #222;
  padding: 0.7rem 0.2rem;
}
.glass-card-group input::placeholder {
  color: rgba(25,118,210,0.85);
  opacity: 0.85;
}
.glass-card-options {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.2rem;
}
.glass-card-remember {
  font-size: 0.97rem;
  color: #fff;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 0.3rem;
}
.glass-card-link {
  color: blue;
  text-decoration: underline;
  font-size: 0.97rem;
  display: flex;
  align-items: center;
  gap: 0.2em;
}
.glass-card-error {
  color: #c73a3a;
  margin-bottom: 0.7rem;
  font-size: 0.97rem;
  text-align: center;
}
.glass-card-btn {
  width: 100%;
  padding: 0.7rem 0;
  background: linear-gradient(90deg, #1976d2 0%, #2196f3 100%);
  border: none;
  border-radius: 7px;
  font-size: 1.08rem;
  font-weight: 600;
  cursor: pointer;
  color: #fff;
  box-shadow: 0 0 8px #90caf9;
  transition: background 0.3s ease, color 0.3s;
  margin-top: 0.2rem;
  margin-bottom: 0.2rem;
  letter-spacing: 1px;
}
.glass-card-btn:hover {
  background: linear-gradient(90deg, #2196f3 0%, #1976d2 100%);
  color: #fff;
  box-shadow: 0 0 16px #1976d2;
}

.glass-support-link {
  text-align: center;
  color: #e3f2fd;
  font-size: 0.95rem;
}
.glass-support-link a {
  color: #e3f2fd; /* match Forgot Password link */
  text-decoration: underline;
  transition: color 0.18s ease;
}

.support-text {
  transition: color 0.2s ease;
  color: #e3f2fd;
}

.glass-support-link a:hover .support-text {
  color: #ffffff;
}

.glass-divider {
  width: 1.5px;
  background: linear-gradient(180deg, #90caf9 0%, #1976d2 100%);
  margin: 2.5rem 0.5rem;
  border-radius: 2px;
  align-self: stretch;
  opacity: 0.25;
}
.glass-info-panel {
  flex: 1.6; /* expand left/info panel */
  display: flex;
  align-items: center;
  justify-content: flex-start; /* anchor to left */
  background: none;
  padding: 0 2.5rem;
  min-width: 320px;
}
.glass-info-content {
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* left aligned content */
  justify-content: center;
  text-align: left;
  color: #fff;
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-size: 16px;
  width: 100%;
  max-width: 420px; /* allow more room for long title */
  padding: 0 12px;
}
.glass-info-logo {
  width: 80px;
  height: 80px;
  margin-bottom: 18px;
  border-radius: 16px;
  background: #fff;
  box-shadow: 0 2px 12px rgba(25, 118, 210, 0.12);
}
.glass-info-title {
  font-size: 2rem; /* slightly larger, fits in expanded panel */
  font-weight: 700;
  font-family: 'Times New Roman', Times, serif;
  margin-bottom: 0.35rem; /* tightened to bring slogan closer */
  color: #fff;
  white-space: nowrap; /* prefer one line on wider screens */
}
.glass-info-title span {
  color: #e3f2fd;
}
.glass-info-title {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.glass-info-desc {
  font-size: 1rem;
  margin-top: 0.2rem; /* small gap under the title */
  margin-bottom: 1.8rem;
  color: #e3f2fd;
}

@media (max-width: 1100px) {
  .glass-split-container {
    flex-direction: column;
    min-height: 100vh;
    width: 100vw;
    border-radius: 0;
    box-shadow: none;
    /* Start content slightly lower so the card can be pulled up visually */
    align-items: center;
    justify-content: flex-start;
    padding-top: 4.5vh;
    padding-bottom: 4.5vh;
  }
  .glass-card-panel, .glass-info-panel {
    padding: 2rem 1.2rem;
    min-width: 0;
    max-width: 100vw;
    border-radius: 0;
    box-shadow: none;
  }
  .glass-info-content {
    max-width: 100vw;
    padding: 0;
  }
  .glass-card {
    max-width: 100vw;
    padding: 1rem 1.2rem;
    border-radius: 18px;
    /* Add a subtle lift effect so the card appears closer to center on mobile */
    margin-top: -48px;
  }
  .glass-divider {
    display: none;
  }
}
