/* Futuristic but simple background */
body {
  margin: 0;
  font-family: "Segoe UI", sans-serif;
  background: linear-gradient(135deg, #0f2027, #203a43, #2c5364);
  color: #fff;
}

/* Container: split screen */
.login-container {
  display: flex;
  height: 100vh;
}

/* Left side: form */
.login-form-section {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 3rem;
  background: rgba(0, 0, 0, 0.4);
}

.login-title {
  font-size: 1.08rem;
  margin-bottom: 1.1rem;
  color: #00e6e6;
  font-weight: 600;
  text-align: center;
  letter-spacing: 0.2px;
}

.login-form .form-group {
  margin-bottom: 1rem;
}

.login-form label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 600;
  text-align: center;
}

.login-form input {
  width: 80%;
  padding: 0.7rem;
  border-radius: 6px;
  border: none;
  outline: none;
  font-size: 1rem;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.form-options {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 1rem 0;
  font-size: 0.9rem;
}

.forgot-link {
  color: #00e6e6;
  text-decoration: none;
}

.forgot-link:hover {
  text-decoration: underline;
}

.error-message {
  color: #ff4d4d;
  margin-bottom: 1rem;
}

.login-btn {
  width: 80%;
  padding: 0.8rem;
  background: #00e6e6;
  border: none;
  border-radius: 6px;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  color: #000;
  transition: background 0.3s ease;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.login-btn:hover {
  background: #00b3b3;
}

/* Right side: logo */
.login-logo-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: rgba(15, 32, 39, 0.6);
  padding: 2rem;
}

.logo-text {
  font-size: 1.35rem;
  text-align: center;
  color: #00e6e6;
  font-weight: 600;
  margin-bottom: 0;
  letter-spacing: 0.5px;
}

.logo-text span {
  color: #fff;
  font-weight: bold;
}

/* logo image styling */
.App-logo {
  max-width: 90px;
  max-height: 90px;
  margin-bottom: 16px;
  border-radius: 8px;
  box-shadow: 0 4px 16px rgba(0,0,0,0.18);
  animation: none !important;
  transform: none !important;
}

/* Responsive: stack on mobile */
@media (max-width: 900px) {
  .login-container {
    flex-direction: column;
    align-items: stretch;
  }
  .login-logo-section, .login-form-section {
    max-width: 50vw;
    min-width: 0;
    padding: 1.2rem 0.7rem 1.5rem 0.7rem;
    box-sizing: border-box;
  }
  .login-form {
    max-width: 50vw;
    padding: 12px 4vw 18px 4vw;
  }
  .App-logo {
    max-width: 60px;
    max-height: 60px;
    margin-bottom: 10px;
  }
  .logo-text {
    font-size: 1rem;
  }
  .login-title {
    font-size: 0.98rem;
  }
}

